import React from "react";
function DropDown(props) {
  return (
    <>
    
          <div className="col-4 mb-3 d-flex justify-content-start align-items-center dropdown-items">
            <img src={props.imgicons} alt="" />
            <p>{props.title}</p>
          </div>
       
    </>
  );
}

export default DropDown;
