import React from "react";
import HeroImg from "../Assests/heroImg1.webp";
function HeroSection() {
  return (
    <>
      <main>
        <div className="container">
          <div className="row">
            <div className="col-lg-6">
              <div className="hero-Content">
                <h1>Software Development Consulting for</h1>
                <p>
                  InvoZone offers software development consulting services for
                  businesses, startups, and enterprises. Partner with a reliable
                  software development company to scale up your engineering
                  capacity.
                </p>
                <button>Schedule a Call</button>
              </div>
            </div>
            <div className="col-lg-6">
              <div className="hero-Img">
                <img className="w-100" src={HeroImg} alt="" />
              </div>
            </div>
          </div>
        </div>
      </main>
    </>
  );
}

export default HeroSection;
